import React, { useRef, useState, useEffect } from "react";
import { injectIntl } from "react-intl";
import { FileIcon, defaultStyles } from 'react-file-icon';
import {
  Row,
  Card,
  CardBody,
  CardHeader,
  FormGroup,
  Label,
  Nav,
  NavItem,
  TabContent,
  TabPane,
} from "reactstrap";
import { NavLink } from 'react-router-dom';
import classnames from 'classnames';
import { Colxx } from "../common/CustomBootstrap";
import IntlMessages from "../../helpers/IntlMessages";
import { downloadFile } from '../../helpers/Utils';
import {bytesToSize} from '../../helpers/Utils';

const RECopywriting = ({ service, orders, downloads, brief, backupNotes, clientView }) => {
  const [activeTab, setActiveTab] = useState('details');
  return (
    <>
      <Row className="chat-row">
        <Colxx md="12" sm="12">
          <h5 className="mb-2">Real Estate Copywriting</h5>
          <p className="text-danger mb-3 text-small">({orders.map((order) => order.name).join(', ')})</p>
          <Card className="mb-4">
            <CardHeader>
              <Nav tabs className="card-header-tabs">
                  <NavItem>
                    <NavLink
                      className={classnames({
                        active: activeTab === 'details',
                        'nav-link': true,
                      })}
                      onClick={() => setActiveTab('details')}
                      to="#"
                      location={{}}
                    >
                      <IntlMessages id="pages.details-title" />
                    </NavLink>
                  </NavItem>
                  <NavItem>
                    <NavLink
                      className={classnames({
                        active: activeTab === 'downloads',
                        'nav-link': true,
                      })}
                      onClick={() => setActiveTab('downloads')}
                      to="#"
                      location={{}}
                    >
                      { clientView === true ? 'Your Files' : 'Client Files' }
                      ({
                        downloads.filter(function(item) {
                          return item.service_option !== 'backup_files';
                        }).length
                      })
                    </NavLink>
                  </NavItem>
                  <NavItem>
                    <NavLink
                      className={classnames({
                        active: activeTab === 'backups',
                        'nav-link': true,
                      })}
                      onClick={() => setActiveTab('backups')}
                      to="#"
                      location={{}}
                    >
                      <IntlMessages id="pages.admin-files" />
                      ({
                        downloads.filter(function(item) {
                          return item.service_option === 'backup_files';
                        }).length
                      })
                    </NavLink>
                  </NavItem>
              </Nav>
            </CardHeader>
            <TabContent activeTab={activeTab}>
              <TabPane tabId="details">
                <Row>
                  <Colxx sm="12">
                    <CardBody>
                      <Row>
                        <Colxx sm="12">
                          {
                            brief.streetAddress && (
                              <p className="m-0 pt-3 pb-2 d-flex align-items-center border-dotted-bottom">
                                <span className="text-theme-1 font-weight-bold">
                                  Street Address
                                </span>
                                <span className="ml-auto">
                                {brief.streetAddress}
                                </span>
                              </p>
                            )
                          }
                        </Colxx>
                        <Colxx sm="12">
                          {
                            brief.streetAddress2 && (
                              <p className="m-0 pt-3 pb-2 d-flex align-items-center border-dotted-bottom">
                                <span className="text-theme-1 font-weight-bold">
                                  Street Address 2
                                </span>
                                <span className="ml-auto">
                                {brief.streetAddress2}
                                </span>
                              </p>
                            )
                          }
                        </Colxx>
                        <Colxx sm="12">
                          {
                            brief.city && (
                              <p className="m-0 pt-3 pb-2 d-flex align-items-center border-dotted-bottom">
                                <span className="text-theme-1 font-weight-bold">
                                  City
                                </span>
                                <span className="ml-auto">
                                {brief.city}
                                </span>
                              </p>
                            )
                          }
                        </Colxx>
                        <Colxx sm="12">
                          {
                            brief.postalCode && (
                              <p className="m-0 pt-3 pb-2 d-flex align-items-center border-dotted-bottom">
                                <span className="text-theme-1 font-weight-bold">
                                  Postal/Zip Code
                                </span>
                                <span className="ml-auto">
                                {brief.postalCode}
                                </span>
                              </p>
                            )
                          }
                        </Colxx>
                        <Colxx sm="12">
                          {
                            brief.country && (
                              <p className="m-0 pt-3 pb-2 d-flex align-items-center border-dotted-bottom">
                                <span className="text-theme-1 font-weight-bold">
                                  Country
                                </span>
                                <span className="ml-auto">
                                {brief.country}
                                </span>
                              </p>
                            )
                          }
                        </Colxx>
                        <Colxx sm="12">
                          {
                            brief.propertyType && (
                              <p className="m-0 pt-3 pb-2 d-flex align-items-center border-dotted-bottom">
                                <span className="text-theme-1 font-weight-bold">
                                  Property Type
                                </span>
                                <span className="ml-auto">
                                {brief.propertyType}
                                </span>
                              </p>
                            )
                          }
                        </Colxx>
                        <Colxx sm="12">
                          {
                            brief.bedNumbers && (
                              <p className="m-0 pt-3 pb-2 d-flex align-items-center border-dotted-bottom">
                                <span className="text-theme-1 font-weight-bold">
                                  Number of bedrooms
                                </span>
                                <span className="ml-auto">
                                {brief.bedNumbers}
                                </span>
                              </p>
                            )
                          }
                        </Colxx>
                        <Colxx sm="12">
                          {
                            brief.bathroomNumbers && (
                              <p className="m-0 pt-3 pb-2 d-flex align-items-center border-dotted-bottom">
                                <span className="text-theme-1 font-weight-bold">
                                  Number of bathrooms
                                </span>
                                <span className="ml-auto">
                                {brief.bathroomNumbers}
                                </span>
                              </p>
                            )
                          }
                        </Colxx>
                        
                        <Colxx sm="12">
                          {
                            brief.floorNumbers && (
                              <p className="m-0 pt-3 pb-2 d-flex align-items-center border-dotted-bottom">
                                <span className="text-theme-1 font-weight-bold">
                                  Number of floors
                                </span>
                                <span className="ml-auto">
                                {brief.floorNumbers}
                                </span>
                              </p>
                            )
                          }
                        </Colxx>
                        
                        <Colxx sm="12">
                          {
                            brief.propertyStyle && (
                              <>
                              <p className="m-0 pt-3 pb-2">
                                <span className="text-theme-1 font-weight-bold">
                                  Property Style
                                </span>
                              </p>
                              <p className="border-dotted-bottom pb-2 m-0">
                                {brief.propertyStyle}
                              </p>
                              </>
                            )
                          }
                        </Colxx>
                        
                        <Colxx sm="12">
                          {
                            brief.carParking && (
                              <>
                              <p className="m-0 pt-3 pb-2">
                                <span className="text-theme-1 font-weight-bold">
                                  Car Parking
                                </span>
                              </p>
                              <p className="border-dotted-bottom pb-2 m-0">
                                {brief.carParking}
                              </p>
                              </>
                            )
                          }
                        </Colxx>
                        
                        <Colxx sm="12">
                          {
                            brief.propertyDetails && (
                              <>
                              <p className="m-0 pt-3 pb-2">
                                <span className="text-theme-1 font-weight-bold">
                                  Further Property Details
                                </span>
                              </p>
                              <p className="border-dotted-bottom pb-2 m-0">
                                {brief.propertyDetails}
                              </p>
                              </>
                            )
                          }
                        </Colxx>
                        
                        <Colxx sm="12">
                          {
                            brief.optionalQuestion && (
                              <>
                              <p className="m-0 pt-3 pb-2">
                                <span className="text-theme-1 font-weight-bold">
                                Have any improvements/renovations been made to the property? If so, what and when?
                                </span>
                              </p>
                              <p className="border-dotted-bottom pb-2 m-0">
                                {brief.optionalQuestion}
                              </p>
                              </>
                            )
                          }
                        </Colxx>
                        
                        <Colxx sm="12">
                          {
                            brief.optionalQuestion2 && (
                              <>
                              <p className="m-0 pt-3 pb-2">
                                <span className="text-theme-1 font-weight-bold">
                                  What are three things that the current owners loved about living there.
                                </span>
                              </p>
                              <p className="border-dotted-bottom pb-2 m-0">
                                {brief.optionalQuestion2}
                              </p>
                              </>
                            )
                          }
                        </Colxx>
                        
                        <Colxx sm="12">
                          {
                            brief.optionalQuestion3 && (
                              <>
                              <p className="m-0 pt-3 pb-2">
                                <span className="text-theme-1 font-weight-bold">
                                  What do you see as the main selling points of the property?
                                </span>
                              </p>
                              <p className="border-dotted-bottom pb-2 m-0">
                                {brief.optionalQuestion3}
                              </p>
                              </>
                            )
                          }
                        </Colxx>

                      </Row>
                    </CardBody>
                  </Colxx>
                </Row>
              </TabPane>
              <TabPane tabId="downloads">
                <Row>
                  <Colxx sm="12">
                    <CardBody>
                      <Row>
                        {downloads.map((download, index) => {
                          if (download.service_option == "basic") {
                            return (
                              <Colxx sm="3" md="2" key={index}>
                                <p
                                  className="mx-auto mb-3"
                                  style={{ width: "80px" }}
                                >
                                  <FileIcon
                                    extension={download.extension.replace(
                                      ".",
                                      ""
                                    )}
                                    {...defaultStyles[
                                      download.extension.replace(".", "")
                                    ]}
                                  />
                                </p>
                                <p className="text-center mb-1 text-truncate">
                                  <a
                                    href="#"
                                    onClick={(e) =>
                                      downloadFile(
                                        download.key_name,
                                        download.original_name
                                      )
                                    }
                                  >
                                    {download.original_name}
                                  </a>
                                </p>
                                <p className="text-center">
                                  {bytesToSize(download.file_size)}
                                </p>
                              </Colxx>
                            );
                          }
                        })}
                      </Row>
                      <Row>
                        {
                          brief.additionalFileLink && (
                            <Colxx sm="12">
                              <span className="text-danger font-weight-bold">File Link:</span>&nbsp;
                              <a className="text-primary" href={brief.additionalFileLink} target="_blank">{brief.additionalFileLink}</a>
                            </Colxx>
                          )
                        }
                      </Row>
                    </CardBody>
                  </Colxx>
                </Row>
              </TabPane>
              <TabPane tabId="backups">
                <Row>
                  <Colxx sm="12">
                    <CardBody>
                      <Row>
                        <Colxx sm="12">
                          <p className="m-0 pt-3 pb-2 d-flex align-items-center text-theme-1 font-weight-bold">
                            <IntlMessages id="briefing.backup-notes" />
                          </p>
                          <p className="mb-5">
                            {backupNotes}
                          </p>
                        </Colxx>
                      </Row>
                      <Row>
                        {
                          downloads.map((download, index) => {
                            if (download.service_option == 'backup_files') {
                              return (
                                <Colxx sm="3" md="2" key={index}>
                                  <p className="mx-auto mb-3" style={{width: '80px'}}>
                                    <FileIcon extension={download.extension.replace('.', '')} {...defaultStyles[download.extension.replace('.', '')]} />
                                  </p>
                                  <p className="text-center mb-1 text-truncate">
                                    <a href='#' onClick={(e) => downloadFile(download.key_name, download.original_name)}>
                                    {download.original_name}
                                    </a>
                                  </p>
                                  <p className="text-center">{bytesToSize(download.file_size)}</p>
                                </Colxx>
                              )
                          }})
                        }
                      </Row>
                    </CardBody>
                  </Colxx>
                </Row>
              </TabPane>
            </TabContent>
          </Card>
        </Colxx>
      </Row>
    </>
  );
};

export default injectIntl(RECopywriting);
